"use client";

import { useState, useCallback } from "react";
import { useMutation } from "convex/react";
import { api } from "@/convex/_generated/api";
import { triggerScan } from "@/app/actions";
import { Upload, Loader2, CheckCircle, AlertCircle } from "lucide-react";
import { motion, AnimatePresence } from "framer-motion";

export function UploadZone() {
    const generateUploadUrl = useMutation(api.scans.generateUploadUrl);
    const createScan = useMutation(api.scans.createScan);

    const [isDragging, setIsDragging] = useState(false);
    const [isUploading, setIsUploading] = useState(false);
    const [uploadStatus, setUploadStatus] = useState<"idle" | "uploading" | "success" | "error">("idle");
    const [errorMessage, setErrorMessage] = useState("");

    const handleDrag = useCallback((e: React.DragEvent) => {
        e.preventDefault();
        e.stopPropagation();
        if (e.type === "dragenter" || e.type === "dragover") {
            setIsDragging(true);
        } else if (e.type === "dragleave") {
            setIsDragging(false);
        }
    }, []);

    const handleDrop = useCallback(async (e: React.DragEvent) => {
        e.preventDefault();
        e.stopPropagation();
        setIsDragging(false);

        const files = e.dataTransfer.files;
        if (files && files[0]) {
            await handleUpload(files[0]);
        }
    }, []);

    const handleFileInput = async (e: React.ChangeEvent<HTMLInputElement>) => {
        if (e.target.files && e.target.files[0]) {
            await handleUpload(e.target.files[0]);
        }
    };

    const handleUpload = async (file: File) => {
        setIsUploading(true);
        setUploadStatus("uploading");
        setErrorMessage("");

        try {
            // 1. Get upload URL
            const postUrl = await generateUploadUrl();

            // 2. Upload file
            const result = await fetch(postUrl, {
                method: "POST",
                headers: { "Content-Type": file.type },
                body: file,
            });

            if (!result.ok) throw new Error("Upload failed");

            const { storageId } = await result.json();

            // 3. Create scan record
            const scanId = await createScan({ storageId });

            // 4. Trigger analysis
            await triggerScan(scanId, ""); // Video URL is optional if storageId is provided

            setUploadStatus("success");
        } catch (err) {
            console.error(err);
            setUploadStatus("error");
            setErrorMessage("Failed to upload. Please try again.");
        } finally {
            setIsUploading(false);
        }
    };

    return (
        <div className="w-full max-w-2xl mx-auto mb-12 relative z-30">
            <div
                onDragEnter={handleDrag}
                onDragLeave={handleDrag}
                onDragOver={handleDrag}
                onDrop={handleDrop}
                className={`
                    relative border-2 border-dashed rounded-xl p-12 text-center transition-all duration-300 backdrop-blur-md
                    ${isDragging
                        ? "border-neon-red bg-neon-red/10 scale-[1.02]"
                        : "border-gray-700 hover:border-gray-500 bg-black/50"
                    }
                `}
            >
                <input
                    type="file"
                    id="file-upload"
                    className="hidden"
                    accept="video/*,image/*"
                    onChange={handleFileInput}
                    disabled={isUploading}
                />

                <AnimatePresence mode="wait">
                    {uploadStatus === "idle" && (
                        <motion.div
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            exit={{ opacity: 0 }}
                            className="flex flex-col items-center gap-4"
                        >
                            <div className="w-20 h-20 rounded-full bg-gray-800 flex items-center justify-center mb-2 group">
                                <Upload className="w-10 h-10 text-gray-400 group-hover:text-neon-red transition-colors" />
                            </div>
                            <div>
                                <h3 className="text-xl font-bold text-white mb-2 font-mono">
                                    DROP EVIDENCE HERE
                                </h3>
                                <p className="text-gray-400 mb-6 font-mono text-sm">
                                    MP4, MOV, JPG, PNG (MAX 50MB)
                                </p>
                            </div>
                            <label
                                htmlFor="file-upload"
                                className="px-8 py-3 bg-neon-red text-black font-bold rounded-full hover:bg-white transition-colors cursor-pointer uppercase tracking-wider"
                            >
                                Select File
                            </label>
                        </motion.div>
                    )}

                    {uploadStatus === "uploading" && (
                        <motion.div
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            exit={{ opacity: 0 }}
                            className="flex flex-col items-center gap-4"
                        >
                            <Loader2 className="w-16 h-16 text-neon-red animate-spin" />
                            <p className="text-xl font-mono text-white animate-pulse">
                                UPLOADING TO SECURE SERVER...
                            </p>
                        </motion.div>
                    )}

                    {uploadStatus === "success" && (
                        <motion.div
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            exit={{ opacity: 0 }}
                            className="flex flex-col items-center gap-4"
                        >
                            <CheckCircle className="w-16 h-16 text-green-500" />
                            <h3 className="text-xl font-bold text-white font-mono">
                                UPLOAD COMPLETE
                            </h3>
                            <p className="text-gray-400 font-mono">
                                ANALYSIS INITIATED.
                            </p>
                            <button
                                onClick={() => setUploadStatus("idle")}
                                className="mt-4 text-sm text-gray-500 hover:text-white underline font-mono"
                            >
                                UPLOAD ANOTHER
                            </button>
                        </motion.div>
                    )}

                    {uploadStatus === "error" && (
                        <motion.div
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            exit={{ opacity: 0 }}
                            className="flex flex-col items-center gap-4"
                        >
                            <AlertCircle className="w-16 h-16 text-red-500" />
                            <h3 className="text-xl font-bold text-white font-mono">
                                UPLOAD FAILED
                            </h3>
                            <p className="text-red-400 font-mono">{errorMessage}</p>
                            <button
                                onClick={() => setUploadStatus("idle")}
                                className="mt-4 px-6 py-2 bg-gray-800 rounded-full hover:bg-gray-700 transition-colors font-mono"
                            >
                                TRY AGAIN
                            </button>
                        </motion.div>
                    )}
                </AnimatePresence>
            </div>
        </div>
    );
}
